.Details {
    display: flex;
    flex-direction: row-reverse;
    width: 960px;
    margin: auto;
}

.Details .Details-info {
    flex: 1;
    text-align: left;
    padding: 0 1em;
}

.Details .Details-title {
    font-size: 2em;
    margin: 0;
}

.Details .Details-image {
    flex: 2;
}

.Details .Details-image img {
    width: 100%;
    padding-left: 10%;
}
